.personal-center {
  padding: 102px 30px 0;
  height: calc(100vh - 60px);
}
.personal-center > .ant-col {
  background: #fff;
  height: 100%;
}
.personal-center > .ant-col:first-child {
  margin-right: 25px;
  padding: 20px;
}
.personal-center .personal-info {
  text-align: center;
  border-bottom: 1px solid #f6f6f6;
  padding-bottom: 17px;
}
.personal-center .personal-info > img {
  width: 89px;
  margin-bottom: 20px;
}
.personal-center .personal-info ul {
  text-align: left;
  margin-left: 30px;
}
.personal-center .personal-info li {
  margin-bottom: 10px;
}
.personal-center .personal-info li img {
  margin-right: 10px;
  vertical-align: -2px;
}
.personal-center .related-professional {
  padding-top: 10px;
}
.personal-center .related-professional p {
  color: #2A283D;
  font-weight: 500;
}
.personal-center .related-professional p img {
  margin-right: 10px;
  vertical-align: -2px;
}
.personal-center .related-professional .major .ant-tag {
  margin-bottom: 15px;
}
.personal-center .setInfo {
  padding: 0 20px;
}
.personal-center .setInfo .ant-tabs-nav {
  color: #000;
}
.personal-center .setInfo .ant-tabs-bar {
  border-bottom: 1px solid #eee !important;
}
.personal-center .setInfo .ant-tabs-tab-active,
.personal-center .setInfo .ant-tabs-tab {
  color: #2A283D !important;
  font-size: 14px;
}
.personal-center .setInfo .ant-tabs-tab-active {
  border-bottom: 4px solid #3E78ED;
}
.personal-center .setInfo .ant-form-item-label {
  margin-right: 20px;
}
.personal-center .setInfo .form-label {
  display: block;
  text-align: right;
  margin-right: 20px;
  line-height: 31px;
}
.personal-center .setInfo .form-label .red {
  margin-right: 5px;
}
.personal-center .setInfo .ant-input:not(.brief) {
  height: 31px;
}
.personal-center .setInfo .brief,
.personal-center .setInfo .ant-input-number-input {
  width: 337px !important;
}
.personal-center .setInfo .ant-form-item-control {
  line-height: 35px;
}
.personal-center .save-set,
.personal-center .upd-major {
  width: 80px;
  height: 29px;
  background: #3E78ED;
  border-radius: 5px;
  color: #fff;
  font-size: 12px;
  line-height: 29px;
  margin-top: 15px;
  border: none;
}
.personal-center .upd-major {
  margin: 0 50%;
  transform: translate(-50%);
}
.personal-center .save-set {
  margin-left: 40px;
}
.personal-center .edit-tel {
  display: inline-block;
  margin-left: 10px;
  color: #3E78ED;
  text-decoration: underline;
  height: 35px;
  line-height: 35px;
  cursor: pointer;
}
.personal-center .textarea-pos {
  position: relative;
}
.personal-center .textarea-pos > span {
  position: absolute;
  right: 0;
  bottom: -4px;
}
.personal-center .ant-radio-checked .ant-radio-inner {
  border: #3E78ED 1px solid;
}
.personal-center .ant-radio-checked .ant-radio-inner::after {
  background-color: #3E78ED;
}
.edit-tel-box .ant-form-item {
  margin-bottom: 20px !important;
}
.edit-tel-box .ant-modal-content {
  padding-bottom: 17px;
}
.edit-tel-box .get-code {
  cursor: pointer;
  color: #FA6400;
  font-size: 12px;
}
.edit-tel-box .ant-form-explain {
  padding-left: 12%;
}
.SetSpecialty .ant-btn {
  background: #FFFFFF;
  border: 1px solid #EBEBEB;
  box-sizing: border-box;
  border-radius: 5px;
  height: 40px;
  width: 90px;
  color: #888888;
  font-size: 16px;
}
.SetSpecialty .ant-btn-primary {
  background: #3E78ED !important;
  color: #FFFFFF;
}
.SetSpecialty .ant-modal-footer {
  display: block;
}
.modal-center-lh .ant-modal-footer {
  justify-content: flex-end !important;
}
.has-error .ant-input-affix-wrapper .ant-input:focus {
  -webkit-box-shadow: none;
  box-shadow: none;
}
.basicInfo-l .ant-form-item-control-wrapper {
  max-width: 338px;
}
